/* checkbox Style */
.checkbox__style {
    position: relative;
    padding-left: 30px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.checkbox__style input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.checkmark {
    position: absolute;
    top: 0;
    left: 0;

    height: 20px;
    width: 20px;
    border: 1px solid #D9D9D9;
    border-radius: 5px;
}

.checkbox__style input:checked~.checkmark {
    background-color: #E9EBFC;
    border: none;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.checkbox__style input:checked~.checkmark:after {
    display: block;
}

.checkbox__style .checkmark:after {
    left: 8px;
    top: 3px;
    width: 5px;
    height: 12px;
    border: solid #0E1A77;
    border-width: 0px 2px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}
/* Spinner CSS */
.spinner_circle {
    display: block;
    margin: auto;
    height: 1.7em;
    width: 1.7em;
    border: 5px solid white;
    border-top-color: transparent;
    border-radius: 50%;
    animation: rotation 0.6s infinite linear;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}